
import React from 'react'
import './Experience.css'

const Experience = ({view, fadeView, setFadeView, delay}) => {

  var test, fade, hide;

  const transition = async () => {await delay(1100); setFadeView('');}

  if (view === 'experience') {
    hide = false;
    test = true;
    fade = false;
  } else if (fadeView === 'experience') {
    test = false;
    fade = true;
    hide = false;
    transition()
  } else {
    test = false;
    fade = false;
    hide = true;
  }

  return (
    <div id={'experience'} className={"page " + (test ? 'fadeIn ' : '') + (fade ? 'fadeOut ' : '') + (hide ? 'hidden' : '')}>
      <div className="Panel">
          {/* <h2>Constant Learning</h2>
          <h2>Attention to Detail</h2>
          <h2>Helping Others</h2>
          <h2>Creative Solutions</h2> */}
      </div>
    </div>
  )
}

export default Experience
