
import React from 'react'
import './Contact.css'

const Contact = ({view, fadeView, setFadeView, delay}) => {

  var test, fade, hide;

  const transition = async () => {await delay(1100); setFadeView('');}

  if (view === 'contact') {
    hide = false;
    test = true;
    fade = false;
  } else if (fadeView === 'contact') {
    test = false;
    fade = true;
    hide = false;
    transition()
  } else {
    test = false;
    fade = false;
    hide = true;
  }

  return (
    <div id={'contact'} className={"page " + (test ? 'fadeIn ' : '') + (fade ? 'fadeOut ' : '') + (hide ? 'hidden' : '')}>
      <div className="Panel">
          {/* <a href="https://www.linkedin.com/in/alexandertcarr/" target="_blank">LinkedIn</a>
          <a href="https://github.com/avacarr" target="_blank">Github</a>
          <a>avacarr68@gmail.com</a> */}
      </div>
    </div>
  )
}

export default Contact
