
import { useState, useEffect } from 'react';
import { NavBar, Background, Signature, Menu } from './components';
import { About, Experience, Contact, More } from './pages';
import './App.css';

function App() {
  const [ view, setView ] = useState('');
  const [ fadeView, setFadeView ] = useState('');

  const delay = async (ms) => {
    return new Promise((resolve) => 
        setTimeout(resolve, ms));
  };

  useEffect(() => {
    console.log('App useEffect active')
    
    console.log('view State: ', view)
    console.log('fadeViewState: ', fadeView)
  },[]);

  return (
    <div className="container">
      <Background/>
      <NavBar setView={setView} view={view} setFadeView={setFadeView} delay={delay}/>
      <Signature/>
      <Menu/>
      <About view={view} fadeView={fadeView} setFadeView={setFadeView} delay={delay}/>
      <Experience view={view} fadeView={fadeView} setFadeView={setFadeView} delay={delay}/>
      <Contact view={view} fadeView={fadeView} setFadeView={setFadeView} delay={delay}/>
      <More view={view} fadeView={fadeView} setFadeView={setFadeView} delay={delay}/>
    </div>
  );
}

export default App;


