
import React from 'react'
import './More.css'

const More = ({view, fadeView, setFadeView, delay}) => {  

  var test, fade, hide;

  const transition = async () => {await delay(1100); setFadeView('');}

  if (view === 'more') {
    hide = false;
    test = true;
    fade = false;
  } else if (fadeView === 'more') {
    test = false;
    fade = true;
    hide = false;
    transition()
  } else {
    test = false;
    fade = false;
    hide = true;
  }

  return (
    <div id={'more'} className={"page " + (test ? 'fadeIn ' : '') + (fade ? 'fadeOut ' : '') + (hide ? 'hidden' : '')}>
      <div className="Panel">
          
      </div>
    </div>
    
  )
}

export default More
