
import React from 'react'
import './About.css'

const About = ({view, fadeView, setFadeView, delay}) => {

  var fadeIn, fadeOut, hide

  const transition = async () => {await delay(1100); setFadeView('');}

  if (view === 'about') {
    hide = false;
    fadeIn = true;
    fadeOut = false;
  } else if (fadeView === 'about') {
    fadeIn = false;
    fadeOut = true;
    hide = false;
    transition()
  } else {
    fadeIn = false;
    fadeOut = false;
    hide = true;
  }

  return (
    <div id={'about'} className={"page " + (fadeIn ? 'fadeIn ' : '') + (fadeOut ? 'fadeOut ' : '') + (hide ? 'hidden ' : '')}>
      <div className="Panel">
          <p>Hello All, </p>
          <p className="indent">My name is Alex Carr, I am a Software Developer transitioning from a Controls Engineer. My interests extend further into Science and Technology. Science has been a consistent learning curve and technology advances at roughly the same rate. In order to create a more advanced and sustainable future, information technology is the key to creating it. For us to utilize and learn in the most effective way we must have more transparency with data and user friendly interfaces to draw from. So I am passionate about helping create that future through the use of new technologies.</p>
      </div>
    </div>
  )
}

export default About
